export default "import React from 'react';\nimport { DayPicker } from 'react-day-picker';\n\nexport default function App() {\n  const style = `\n    .caption_aqua { \n      color: aquamarine; \n      font-weight: bold; \n      font-size: 140%; \n    }\n  `;\n  return (\n    <>\n      <style>{style}</style>\n      <DayPicker\n        classNames={{\n          caption_label: 'caption_aqua'\n        }}\n      />\n    </>\n  );\n}\n";