export default "import React, { useState } from 'react';\n\nimport { DayClickEventHandler, DayPicker } from 'react-day-picker';\n\nexport default function App() {\n  const initialFooter = <p>Try clicking the todayâ€™s date.</p>;\n  const [footer, setFooter] = useState(initialFooter);\n\n  const handleDayClick: DayClickEventHandler = (day, modifiers) => {\n    if (modifiers.today) {\n      setFooter(<p>You clicked the todayâ€™s date.</p>);\n    } else {\n      setFooter(initialFooter);\n    }\n  };\n\n  return <DayPicker onDayClick={handleDayClick} footer={footer} />;\n}\n";