export default "import React from 'react';\nimport { DayPicker } from 'react-day-picker';\n\nexport default function App() {\n  const hiddenDays = [\n    new Date(2022, 5, 10),\n    new Date(2022, 5, 20),\n    new Date(2022, 5, 11)\n  ];\n\n  return <DayPicker defaultMonth={hiddenDays[0]} hidden={hiddenDays} />;\n}\n";