export default "import React from 'react';\nimport { DayPicker } from 'react-day-picker';\n\nconst bookedDays = [\n  new Date(2021, 5, 8),\n  new Date(2021, 5, 9),\n  new Date(2021, 5, 11)\n];\n\nconst style = `\n  .my-booked-class {\n    color: tomato;\n  }\n`;\n\nexport default function App() {\n  return (\n    <>\n      <style>{style}</style>\n      <DayPicker\n        defaultMonth={bookedDays[0]}\n        modifiers={{\n          booked: bookedDays\n        }}\n        modifiersClassNames={{\n          booked: 'my-booked-class'\n        }}\n      />\n    </>\n  );\n}\n";